.page {
  display: flex;
  justify-content: center;
  align-items: center;
}

.wrap {
  box-sizing: border-box;
  margin: 30px 0 30px 0;
  padding: 30px 30px 0 30px;
  overflow: auto;
  box-shadow: 0 0 50px #ccc;
  width: 960px;
}


@media screen and (max-width: 1000px) {
  .wrap {
    margin: 0;
    box-shadow: none;
    width: 100%;
  }
}
